.App {
  text-align: center;
  background: url(./accomplishment-adult-adventure-372098.jpg) no-repeat center center fixed;  
  min-width: 99%;
  min-height: 99%;
  position: absolute;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(0, 0, 0);
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.card {
  display: inline-block;
  text-align: center;
  width: 3em;
  font-size: 2em;
  -webkit-box-shadow: 0px 0px 8px 7px rgb(255, 0, 0);
          box-shadow: 0px 0px 8px 7px rgb(255, 0, 0);
  margin: 1em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.activeCard { 
  color: rgb(255, 230, 2); 
  background: rgb(255, 0, 149); 
}

.fb-signin-button {
  /* This is where you control how the button looks. Be creative! */
  display: inline-block;
  padding: 4px 8px;
  border-radius: 3px;
  background-color: #4267b2;
  color: #fff;
}



